.panel-container {
	color: #02fcbd;
	max-height: 100%;
	overflow: auto;
	padding-bottom: 10px;
	-ms-flex-positive: 1;
	    flex-grow: 1;
}

.console-text-container {
	width: 100%;
	min-height: 300px;
}

.typed-text {
	
}

.console-text {
	overflow: auto;
	white-space: pre-wrap;
	width: 100%;
	max-height: 100%;
}

.text-container {
	width: 100%;
	max-height: 100%;
}

.audio-container {
	position: absolute;
	visibility: hidden;
}

.start-button {
	-webkit-animation: fadein 0.7s; 
            animation: fadein 0.7s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}
