.scripts-panel-header {
	color: #02fcbd;
}

.scripts-form {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.scripts-input {
	font-family: 'Orbitron', sans-serif;
	
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	height: 300px;
	
	padding: 10px;
	border: 2px solid white;
	margin: 20px 0px 20px 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;

	background-color: black;
	color: white;
	font-size: 14px;
}

.scripts-error {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 100%;
	height: 40px;
	color: red;
}

.scripts-error-hidden {
	visibility: hidden;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}


