
.grid-controls-container {
	position: fixed;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;

	right: 0;
	top: 0;
	width: 120px;
	height: 200px;

	/*border-left: 2px solid white;
	border-bottom: 2px solid white;*/
	/*background-color: rgba(100, 100, 100, 0.5);*/

	-webkit-transition: right 1s;
	-o-transition: right 1s;
	transition: right 1s;
}

.grid-controls-hide {
	right: -150px;
}


.grid-control-x-group, .grid-control-y-group, .grid-control-z-group {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
	width: 100%;
	font-size: 10px;
}

.rotation-value {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;

	width: 42px;
	height: 34px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;

	color: white;
}

.rotation-value-frozen {
	color: grey;
}

.grid-control {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;

	border: 1px solid #02fcbd;
	color: #02fcbd;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 25px;
	height: 25px;
	cursor: pointer;
}

.grid-control:hover {
	background-color: white;
}

.grid-controls-frozen {
	border: 1px solid grey;
	color: grey;
	cursor: default;
	pointer-events: none;
}

.grid-controls-frozen:hover {
	background-color: rgba( 0,0,0,0);
}

.value-label {
	width: 30px;
}


















