.game-view {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	overflow: auto;
	font-family: 'Orbitron', sans-serif;
}

.game-view-overlay {
	position: absolute;
	width: 1920px;
	height: 1280px;
	background-color: black;
	z-index: 1;

	-webkit-transition: opacity 6s;
	-o-transition: opacity 6s;
	transition: opacity 6s;
}

.hide {
	opacity: 0;
	pointer-events: none;
}

.layer-1 {
	position: absolute;
	width: 1920px;
	height: 1280px;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.menu-icon {
	margin-top: 10px;
	position: fixed;
	width: 50px;
	cursor: pointer;
	opacity: 1;

	-webkit-transition: opacity 1s;
	-o-transition: opacity 1s;
	transition: opacity 1s;
}

.menu-icon-hide {
	opacity: 0;
}

.background-image {
	position: absolute;
}

.object2 {
	position: absolute;
	width: 40px;
	top: 100px;
	left: 1800px;
}

.object3 {
	position: absolute;
	width: 100px;
	top: 600px;
	left: 900px;
}

.interact {
	cursor: pointer;
}

.hidden {
	visibility: hidden;
}



