.footer {
	width: 100%;
	min-height: 55px;
	margin-bottom: -55px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	font-family: 'Orbitron', sans-serif;

	background-color: black;
	border-top: 2px solid white;

	-webkit-transition: margin-bottom 1s;
	-o-transition: margin-bottom 1s;
	transition: margin-bottom 1s;

	overflow: hidden;
	z-index: 2;
}

.footer-open {
	margin-bottom: 0;
}

.right {
	width: 400px;
	padding: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.test-controls {
	position: absolute;
	left: 0;
	visibility: hidden;
	padding: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.test-controls-show {
	visibility: visible;
}

.test-button {
	width: 100px;
	height: 30px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	cursor: pointer;

	background-color: grey;
	border: 2px solid red;
	color: white;
}

.sfx-audio {
	visibility: hidden;
}


