.menu-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;

	padding: 20px;
	width: 380px;
	margin-left: 0px;
	background-color: black;
	border-right: 2px solid white;
	line-height: 25px;
	font-family: 'Orbitron', sans-serif;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;

	-webkit-transition: margin-left 0.5s;
	-o-transition: margin-left 0.5s;
	transition: margin-left 0.5s;

	z-index: 2;
}

.menu-container.closed {
	margin-left: -380px;
}

.console, .inventory, .scripts {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	background-color: black;
	width: 100%;
	height: 100%;
}

.third-panel {
	color: red;
}


.bottom-menu {
	height: 40px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	color: white;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	padding-top: 10px;

	border-top: 1px dotted #02fcbd;
	box-sizing: border-box;
}

.bottom-menu-hide {
	opacity: 0;
}


