.button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;

	cursor: pointer;
	color: #02fcbd;
	width: 105px;
	height: 30px;
	border-radius: 20px;
}

.button-border {
	border: 1px solid #02fcbd;
}

.button-border:hover {
	background-color: rgba(2, 252, 189, 0.5);
}

.button-selected {
	border: 1px solid #02fcbd;
	border-radius: 20px;
	background-color: rgba(2, 252, 189, 0.5);
}

.button-hover {
	border: 1px solid #02fcbd;
}

.button:hover {
	/*border: 1px solid #02fcbd;*/
}

.button-hover:hover {
	background-color: rgba(2, 252, 189, 0.5);
}


.freeze-button {
	border: 1px solid #02fcbd;
}



